import React, { Component } from "react";
import Slide from "react-reveal";

class Portfolio extends Component {
  render() {
    if (!this.props.data) return null;

    const researchcomp = this.props.data.research.map(function (research) {
      return (
        <div key={research.title}>
          <h2><em>{research.title}</em></h2>
          <p className="info">
            {research.site}
            <span>&bull;</span> <em className="date">{research.status}</em>
            <a href={research.link} target="_blank" rel="noreferrer">&emsp; Link</a>
          </p>
          <p>{research.cite}</p>
        </div>
      );
    });

    return (
      <section id="portfolio">
        <Slide left duration={1300}>
          <div className="row work">
            <div className="three columns header-col">
              <h3>
                <span>Research</span>
              </h3>
            </div>

            <div className="nine columns main-col">{researchcomp}</div>
          </div>
        </Slide>
      </section>
    );
  }
}

export default Portfolio;
